import React from 'react'

const ViewProductDetails = () => {


  return (
    <section className="min-h-screen bg-slate-100 px-8 py-16 flex flex-col md:flex-row gap-10">
      {/* Image */}
      <div className="flex-1 flex items-center justify-center">
        <img
          src={product.images}
          alt={product.name}
          className="rounded-3xl shadow-2xl w-full max-w-md object-cover"
        />
      </div>

      {/* Details */}
      <div className="flex-1 text-slate-800">
        <h1 className="text-4xl font-bold mb-4">{product.name}</h1>
        <div className="flex items-center mb-4">
          <span className="text-yellow-400 text-xl">⭐⭐⭐⭐☆</span>
          <span className="ml-2 text-gray-500">(120 reviews)</span>
        </div>
        <p className="text-lg mb-2">
          <span className="font-semibold">Brand:</span> {product.brand}
        </p>
        <p className="text-lg mb-2">
          <span className="font-semibold">Category:</span> {product.category}
        </p>
        <p className="text-lg mb-2">
          <span className="font-semibold">Stock:</span> {product.stock}
        </p>
        <p className="text-3xl font-bold text-slate-900 my-4">
          ₹{product.price}
        </p>
        <p className="text-gray-700 mb-6">{product.description}</p>

        <div className="flex gap-4">
          <button className="px-6 py-3 bg-slate-800 text-white rounded-full hover:bg-slate-700 transition">
            Add to Cart
          </button>
          <button className="px-6 py-3 bg-red-500 text-white rounded-full hover:bg-red-600 transition">
            ❤️ Wishlist
          </button>
        </div>
      </div>
    </section>
  );
};

  
export default page
